module ofx4js.domain.data.common {

/**
 * Common details about an account.
 *
 * @author Ryan Heaton
 */
export interface AccountDetails {

  /**
   * The account number.
   *
   * @return The account number.
   */
  getAccountNumber(): string;

  /**
   * The account key.
   *
   * @return The account key.
   */
  getAccountKey(): string;

}

}



